/*-----------------------------------
#   Login Page CSS 
------------------------------------*/
.auth-form{
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../../images/backgrounds/auth-bg.png');
    margin-top: 86px;
}

.form-box{
    max-width: 680px;
    margin: 0 auto;
    background-color: #fff;
    padding: 40px 70px;
    border: 1px solid #f2f2f2;
}

.f-ps-link{
    font-size: 18px;
    color: #2F80ED;
}

.form-box .form-submit-btn{
    margin-top: 50px;
    margin-bottom: ;
}
.social-login{
    display: flex;
    margin: 40px -15px;
}
.social-login li{
    padding: 0 15px;
    flex: 1;
}
.social-login  a{
    width: 100%;
    height: 52px;
    border-radius: 2px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}
.social-login  a.google{
    border: 1px solid #ECECEC;
    background-color: #fff;
}
.social-login  a.fb{
    background-color: #1877F2;
}
.social-login  a.apple{
    background-color: #000000;
}

.goto{
    font-size: 16px;
    font-weight: 600;
    color: #545454;
}
.goto > a {
    color: #2F80ED;
}


.password_toggle_btn{
    background-color: transparent;
    border: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 15px;
    font-size: 20px;
    color: #B0B0B0;
}


