.icon-group{
    position: relative;
}

.otp-input.form-control{
    width: 60px !important;
    height: 60px;
    display: inline-flex;
    background-color: #FFF;
    border-radius: 4px;
    font-size: 16px;
}

.otpContainer > div{
    padding-right: 30px;
}
.otpContainer > div:last-child{
    padding-right: 0;
}


.otp-metas{
    font-size: 16px;
    color: #B0B0B0;
    margin-top: 20px;
}
.otp-metas button{
    color: #2F80ED;
    background-color: transparent;
    border:0;
}

.otp-timer{
    position: relative;
}
.otp-timer::before{
    content: url('../../images/icons/ic-timer.svg');
    vertical-align: middle;
    margin-right: 5px;
}

@media screen and (max-width: 767px){
    .form-box{
        padding: 30px 20px;
    }
}



@media screen and (max-width:479px){
    .otp-input.form-control{
        width: 52px !important;
        height: 52px;
    }
}